<template>
    <div id="product-display" @click.self="visible = false" v-bind:style="{ opacity: visible == true ? 1 : 0, pointerEvents: visible == true ? 'all' : 'none' }">
        <div id="product-page">
            <div id="product-header">
                <p>{{ name }}</p>
                <p @click.self="visible = false">X</p>
            </div>
            <img src="../../assets/test.jpg">
            <div id="product-buttons">
                <div id="item-amount">
                    <div id="amount-container">
                        <p>{{ amount }}</p>
                    </div>
                    <div id="amount-changer">
                        <button type="button" @click="amount++">+</button>
                        <button type="button" @click="amount--">-</button>
                    </div>
                </div>
                <button type="button">Köp</button>
            </div>
        </div>
    </div>
</template>

<script>
import { productDisplay } from '@/mixins/productDisplay.js';

export default {
    name: 'ProductDisplay',
    
    mixins: [ productDisplay ],
    
    mounted() {
        
    },
    
    methods: {
        open() {
            
        }
    }
}
</script>

<style scoped>

#product-display
{
    position: fixed;
    
    width: 100vw;
    height: 100vh;
    
    background-color: rgba(0, 0, 0, 0.6);
    
    z-index: 1000;
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
    pointer-events: all;
    opacity: 1;
    transition: .2s ease-in-out;
}

#product-page
{
    padding: 8px;
    
    width: calc(100% - 64px);
    
    background-color: white;
}

#product-header
{
    margin-bottom: 8px;
    
    font-family: 'Oswald', sans-serif;
    
    display: flex;
    justify-content: space-between;
}

#product-page > img
{
    width: 100%;
    height: auto;
}

#product-buttons
{
    margin-top: 6px;    
    display: flex;
    justify-content: space-between;
    
    gap: 8px;
}

#product-buttons > *
{
    width: 100%;
    height: 50px;
}

#product-buttons > button
{
    border: none;
    background-color: black;
    color: white;
}



#item-amount
{
    padding: 1px;
    
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    gap: 1px;
    
    background-color: black;
}

#amount-container
{
    display: flex;
    justify-content: center;
    align-items: center;
    
    width: 100%;
    height: 100%;
    
    background-color: white;
}

/* The +/- buttons */
#amount-changer
{
    height: 100%;
    width: 35px;
    
    display: flex;
    flex-direction: column;
    gap: 1px;
}

#amount-changer > *
{
    height: 100%;
