/* Doing this to keep it globally avaliable */
var data = {
    name: '?',
    visible: false,
    
    amount: 1
}

export const productDisplay = {
    data() {
        return data;
    },
    
    methods: {
        openProduct(name) {
            this.name = name;
            this.visible = true;
            
            this.amount = 1;
        },
    },
}